.modalContent {
  display: grid;
  place-items: center;
  padding-top: 7rem;
  width: 100%;
}
.ModalBody {
  background: #000000;
  max-width: 550px;
  width: 90%;
  position: relative;
  border-radius: 29px;
  display: flex;
  color: #ffffff;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
  align-items: center;
}

.modalOverlay {
  width: 100vw;
  height: 100vh;
  background-color: rgba(27, 27, 27, 0.8);
}
.replayModal {
  width: 100%;
  border: 3px solid #37e710;
  background-color: #000000;
  max-width: 100%;
  padding: 1rem 0;
  border-radius: 29px;
}

/* orderBoxWrapper */
.orderBoxWrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  padding: 20px 0;
}
.orderBoxContent {
  display: flex;
  gap: 0.8rem;
  width: 90%;
  flex-direction: column;
}

.orderBoxRowBox {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.orderItemRowItem {
  flex: 1;
}
.orderBoxRowBox input {
  background: #000000;
  padding: 0 10px;
  border: 0.3px solid #b1b1b1;
  color: #b1b1b1;
  outline: none;
  border-radius: 5px;
  height: 40px;
}
/* orderBottomInfo */
.orderBottomInfo {
  height: 46.86px;

  background: #000000;
  border: 0.462449px solid #b1b1b166;
  padding: 0 20px;
  display: flex;
  flex: 1;
  border-radius: 5px;
  align-items: center;
  gap: 10px;
  font-family: "K2D";
  font-style: normal;
  font-weight: 600;
  font-size: 11.1857px;
  line-height: 21px;
  /* identical to box height */

  color: #37e710;
}
.orderNowBtnPost {
  width: fit-content;
  font-size: 15px;
  height: 46px;
  padding: 0 10px;

  background: #37e710;
  border: 0.520613px solid #37e710;
}
.orderBottomBox {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  align-items: center;
}
/* closeImg */

.closeImg {
  position: absolute;
  right: 1rem;
  top: 1rem;
  height: 30px;
  border-radius: 50%;

  border: 1px solid #37e710;
  cursor: pointer;
  width: 30px;
}
/* /* fadeOrderBtn */
.fadeOrderBtn {
  opacity: 0.4;
  pointer-events: none !important;
}
/* orderPaymentElement */
.orderPaymentElement {
  padding: 2rem;
}
.orderBottomInfo div {
  flex: 1;
}
@media screen and (min-width: 480px) and (max-width: 600px) {
  .modalContent {
    padding-top: 2rem !important;
  }
  .orderBoxWrapper {
    padding: 10px 0;
  }
}

@media screen and (max-width: 480px) {
  .orderBoxWrapper {
    padding: 10px 0;
  }
  .orderPaymentElement {
    padding: 5px !important;
  }
  .orderBottomBox {
    gap: 1rem !important;
    flex-direction: column !important;
  }
  .modalContent {
    padding-top: 2rem !important;
  }
  .orderBottomInfo {
    padding: 0 !important
    ;
    justify-content: space-between !important;
    width: 100%;
  }
  .orderBottomInfo div:nth-of-type(1) {
    letter-spacing: 1px;
  }
  .orderBottomInfo div {
    flex: 1;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  .orderNowBtnPost {
    height: 37px;
    font-size: 13px;
    letter-spacing: 1px;
  }
  .orderBoxHeader h2 {
    font-size: 20px;
  }
}
