.messages_ground {
  max-height: 100%;
  overflow: scroll;

  flex: 1;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.messages_ground::-webkit-scrollbar {
  display: none;
}

@media screen and (min-width: 1024px) {
  .messages_ground {
    padding: 0;
  }
}
